import React from 'react';
import {makeStyles} from "@mui/styles";
import AddIcon from '@mui/icons-material/Add';
import {Button} from "@mui/material";

const useStyles = makeStyles((theme) => ({
  cardWrap: {
    margin: 10,
    padding: 10,
    borderRadius: 10,
    background: '#eeeeee',
    height: 120,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between'
  },
  cardTitle: {
    display: 'flex',
    flexGrow: 1,
    margin: 30,
  },
  priceDiv: {
    display: 'flex',
    alignItems: 'center'
  },
}));

const Card = ({item, openBlock, selectProduct}) => {
  const classes = useStyles();

  return (
      <div className={classes.cardWrap} >
        <img
            src={item.asset_url}
            style={{height: '120px', width: '120px'}}
            alt=''
        />
        <h4 className={classes.cardTitle}>{item.title}</h4>
        <div className={classes.priceDiv}>
          <span>{item.price_amount} грн</span>
          <Button onClick={() => {selectProduct(item); openBlock()}}>
            <AddIcon />
          </Button>

        </div>
      </div>
  );
};

export default Card;