import React, {useEffect, useState} from 'react';
import { makeStyles } from '@mui/styles';
import Dropdown from "./Dropdown";
import Gaisers from '../products/Gaisers.json';
import Jiggers from '../products/Jiggers.json';
import Madlers from '../products/Madlers.json';
import Shakers from '../products/Shakers.json';
import Spoons from '../products/Spoons.json';
import Squisers from '../products/Squisers.json';
import Strainers from '../products/Strainers.json';

interface ListInterface {
    items: object[];
    // pages: string[];
}

const useStyles = makeStyles((theme) => ({
  setPreview: {
    background: 'white',
    height: 120,
    borderRadius: 15,
    margin: '10px 0 30px',
  },
  categoriesBlock: {
    background: 'white',
    borderRadius: 15,
    margin: '10px 0',
    padding: 10,
  },

  selectedImage: {
    width: 100,
    height: 100,
    margin: 10,
  }
}));

const Constructor = () => {

  const [shaker, setShaker] = useState<any>(null);
  const [jigger, setJigger] = useState<any>(null);
  const [stainer, setStainer] = useState<any>(null);
  const [madler, setMadler] = useState<any>(null);
  const [spoons, setSpoons] = useState<any>(null);
  const [squisers, setSquisers] = useState<any>(null);

  const classes = useStyles();



  return (
      <div>
        <div className={classes.setPreview}>
          <img className={classes.selectedImage} src={shaker && shaker.asset_url} alt='' />
          <img className={classes.selectedImage} src={jigger && jigger.asset_url} alt='' />
          <img className={classes.selectedImage} src={stainer && stainer.asset_url} alt='' />
          <img className={classes.selectedImage} src={madler && madler.asset_url} alt='' />
          <img className={classes.selectedImage} src={spoons && spoons.asset_url} alt='' />
          <img className={classes.selectedImage} src={squisers && squisers.asset_url} alt='' />
        </div>
        <h3>Собери свой уникальный набор</h3>
        <div className={classes.categoriesBlock}>
          <Dropdown title={'Шейкер'} categoryId={251685} selectProduct={setShaker} selectedProduct={shaker} items={Shakers} />
          <Dropdown title={'Джиггер'} categoryId={255321} selectProduct={setJigger} selectedProduct={jigger} items={Jiggers} />
          <Dropdown title={'Cтрейнер'} categoryId={255320} selectProduct={setStainer} selectedProduct={stainer} items={Strainers} />
          <Dropdown title={'Мадлер'} categoryId={255323} selectProduct={setMadler} selectedProduct={madler} items={Madlers} />
          <Dropdown title={'Ложки'} categoryId={255323} selectProduct={setSpoons} selectedProduct={spoons} items={Spoons} />
          <Dropdown title={'Сквизеры'} categoryId={255323} selectProduct={setSquisers} selectedProduct={squisers} items={Squisers} />
        </div>

      </div>
  );
};

export default Constructor;